import * as m from 'mathjs'
//@ts-ignore
import linspace from 'linspace'
import Graph from '../graph/Graph'
import { useState } from 'react'
import { Button, NavigationBar, UL } from '../containers'
const ZAD1 = () => {
  ///KOD ŹRÓDŁOWY DO ZESTAWU #3----------------------------------------
  const f: number = 10
  const fs: number = 8000
  const phi: number = 2 * m.pi
  const Tc: number = 2
  const N: number = Tc * fs

  const n: Array<number> = linspace(0, Tc, N)

  const x = (t: number) => {
    return (1 - t) * m.sin(2 * m.pi * f * t + phi) * m.cos(4 * m.pi * t)
  }

  const y = (t: number) => {
    //@ts-ignore
    return m.pow(t, 3) - 1 + m.cos(4 * t * t * m.pi) * t
  }

  const z = (t: number) => {
    return x(t) / (m.abs(y(t) * m.cos(5 * t) - x(t) * y(t)) + 3)
  }

  const v = (t: number) => {
    return (x(t) * 662) / (m.abs(x(t) - y(t)) + 0.5)
  }
  const graph1: Array<object> = []
  const graph2: Array<object> = []
  const graph3: Array<object> = []

  n.forEach(element => {
    graph1.push({ x: element, y: y(element) })
  })
  n.forEach(element => {
    graph2.push({ x: element, y: z(element) })
  })
  n.forEach(element => {
    graph3.push({ x: element, y: v(element) })
  })
  ///---------------------------------------------------------------------

  const [graph, setGraph] = useState(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <li>
            <Button
              className={graph === 1 ? 'activeclass' : ''}
              onClick={() => setGraph(1)}
            >
              Wykres I
            </Button>
          </li>
          <li>
            <Button
              className={graph === 2 ? 'activeclass' : ''}
              onClick={() => setGraph(2)}
            >
              Wykres II
            </Button>
          </li>
          <li>
            <Button
              className={graph === 3 ? 'activeclass' : ''}
              onClick={() => setGraph(3)}
            >
              Wykres III
            </Button>
          </li>
        </UL>
      </NavigationBar>
      {graph === 1 && (
        <Graph
          data={graph1}
          Title={'Wykres I'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 2 && (
        <Graph
          data={graph2}
          Title={'Wykres II'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 3 && (
        <Graph
          data={graph3}
          Title={'Wykres III'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
    </>
  )
}

export default ZAD1
