import * as m from 'mathjs'
//@ts-ignore
import linspace from 'linspace'
import Graph from '../graph/Graph'
import { Button, NavigationBar, UL } from '../containers'
import { useState } from 'react'

const ZAD4 = () => {
  const fs: number = 22050
  const Tc: number = 1
  const N: number = Tc * fs
  const n: Array<number> = linspace(0, Tc, N)

  const b1 = (t: number) => {
    var sum: number = 0
    for (let h = 1; h <= 5; h++) {
      //@ts-ignore
      sum += (m.pow(-1, h) / h) * m.sin(h * m.pi * t * t)
    }
    return sum
  }
  const b2 = (t: number) => {
    var sum: number = 0
    for (let h = 1; h <= 20; h++) {
      //@ts-ignore
      sum += (m.pow(-1, h) / h) * m.sin(h * m.pi * t * t)
    }
    return sum
  }
  const b3 = (t: number) => {
    var sum: number = 0
    for (let h = 1; h <= 50; h++) {
      //@ts-ignore
      sum += (m.pow(-1, h) / h) * m.sin(h * m.pi * t * t)
    }
    return sum
  }
  const graph1: Array<object> = []
  const graph2: Array<object> = []
  const graph3: Array<object> = []
  n.forEach(element => {
    graph1.push({ x: element, y: b1(element) })
  })
  n.forEach(element => {
    graph2.push({ x: element, y: b2(element) })
  })
  n.forEach(element => {
    graph3.push({ x: element, y: b3(element) })
  })
  const [graph, setGraph] = useState(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <li>
            <Button
              className={graph === 1 ? 'activeclass' : ''}
              onClick={() => setGraph(1)}
            >
              Wykres I
            </Button>
          </li>
          <li>
            <Button
              className={graph === 2 ? 'activeclass' : ''}
              onClick={() => setGraph(2)}
            >
              Wykres II
            </Button>
          </li>
          <li>
            <Button
              className={graph === 3 ? 'activeclass' : ''}
              onClick={() => setGraph(3)}
            >
              Wykres III
            </Button>
          </li>
        </UL>
      </NavigationBar>
      {graph === 1 && (
        <Graph
          data={graph1}
          Title={'Wykres I'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 2 && (
        <Graph
          data={graph2}
          Title={'Wykres II'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
      {graph === 3 && (
        <Graph
          data={graph3}
          Title={'Wykres III'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
    </>
  )
}

export default ZAD4
