import * as m from 'mathjs'
//@ts-ignore
import linspace from 'linspace'
import Graph from '../graph/Graph'
import { useState } from 'react'
import { Button, NavigationBar, UL } from '../containers'
const ZAD3 = () => {
  ///KOD ŹRÓDŁOWY DO ZESTAWU #1----------------------------------------
  const fs: number = 8000
  const Tc: number = 1
  const N: number = Tc * fs
  const n: Array<number> = linspace(0, Tc, N)

  const u = (t: number) => {
    if (t < 0.1 && t >= 0) return m.sin(6 * m.pi * t) * m.cos(5 * m.pi * t)
    if (t < 0.4 && t >= 0.1) return -1.1 * t * m.cos(41 * m.pi * t * t)
    //@ts-ignore
    if (t < 0.72 && t >= 0.4) return t * m.cos(20 * m.pow(t, 4))
    if (t < 1 && t >= 0.72) return 3.3 * (t - 0.72) * m.cos(27 * t + 1.3)
  }
  const data: Array<object> = []
  n.forEach(element => {
    data.push({ x: element, y: u(element) })
  })
  ///------------------------------------------------------------------
  const [graph, setGraph] = useState(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <li>
            <Button
              onClick={() => setGraph(1)}
              className={graph === 1 ? 'activeclass' : ''}
            >
              Wykres I
            </Button>
          </li>
        </UL>
      </NavigationBar>
      {graph === 1 && (
        <Graph
          data={data}
          Title={'Wykres I'}
          xTitle="Czas"
          yTitle={'Wartość funkcji'}
        />
      )}
    </>
  )
}

export default ZAD3
