import styled from 'styled-components'

interface sizeProps {
  windowSize: {
    width: string
    height: string
  }
}

export const Wrapper = styled.div`
  width: ${({ windowSize }: sizeProps) => windowSize.width};
  height: ${({ windowSize }: sizeProps) => windowSize.height};
  margin: 0 auto;
`
export const NavigationBar = styled.div`
  width: 9%;
  height: 100%;
  float: left;
`
export const GraphDiv = styled.div`
  width: 88%;
  height: 100%;
  float: left;
`

export const Button = styled.button`
  &.activeclass {
    background: #007acc;
  }
  border: 0.2rem solid #007acc;
  cursor: pointer;
  width: 6rem;
  height: 2.5rem;
  border-radius: 0.3rem;
  background: #eee;
  &:hover {
    background: #007acc;
  }
  transition-duration: 0.6s;
`
export const UL = styled.ul`
  list-style-type: none;
  li {
    margin-bottom: 2rem;
  }
`
export const NavLinkWrappers = styled.div`
  margin-top: 2rem;
  width: 29rem;
  height: 3rem;
  background: grey;
  display: flex;
  align-items: center;
  border-radius: 0.3rem;
  a {
    display: inline-block;
    color: #000;
    font-family: arial;
    width: 5rem;
    font-size: 1.1rem;
    heigth: 1rem;
    margin-left: 2rem;
    text-decoration: none;
    &:hover {
      border-bottom: 0.1rem solid #eee;
    }
    &.active {
      border-bottom: 0.1rem solid #eee;
    }
  }
`
