import * as m from 'mathjs'
//@ts-ignore
import linspace from 'linspace'
import Graph from '../graph/Graph'
import { Button, NavigationBar, UL } from '../containers'
import { useState } from 'react'

const ZAD1 = () => {
  ///KOD ŹRÓDŁOWY DO ZESTAWU #8----------------------------------------
  const f: number = 10
  const phi: number = 2 * m.pi
  const fs: number = 8000
  const Tc: number = 2
  const N: number = Tc * fs
  const n: Array<number> = linspace(0, Tc, N)
  const x = (t: number) => {
    return (1 - t) * m.sin(2 * m.pi * f * t + phi) * m.cos(4 * m.pi * t)
  }
  const data: Array<object> = []
  n.forEach(element => {
    data.push({ x: element, y: x(element) })
  })
  ///------------------------------------------------------------------
  const [graph, setGraph] = useState(1)
  return (
    <>
      <NavigationBar>
        <UL>
          <li>
            <Button
              className={graph === 1 ? 'activeclass' : ''}
              onClick={() => setGraph(1)}
            >
              Wykres I
            </Button>
          </li>
        </UL>
      </NavigationBar>
      {graph === 1 && (
        <Graph
          data={data}
          Title={'Wykres I'}
          xTitle="Czas"
          yTitle="Wartość funkcji"
        />
      )}
    </>
  )
}

export default ZAD1
